import React, { Component } from "react";
import Button from "../../../Components/UI/Button/Button";
import classes from "./ContactData.css";
import axios from "../../../axios-orders";
import Spinner from "../../../Components/UI/Spinner/Spinner";
import Input from "../../../Components/UI/Input/Input";

class ContactData extends Component {
  state = {
    orderForm: {
      name: {
        elementtype: "input",
        elementConfig: {
          type: "text",
          placeholder: "Your Name",
          required: true,
        },
        value: "",
        valid: false,
      },
      street: {
        elementtype: "input",
        elementConfig: {
          type: "text",
          placeholder: "Street",
          required: true,
        },
        value: "",
        valid: false,
      },
      zipcode: {
        elementtype: "input",
        elementConfig: {
          type: "text",
          placeholder: "ZIP code (6 digit)",
          required: true,
          pattern: "^.{6}$",
        },
        value: "",
        valid: false,
      },
      country: {
        elementtype: "input",
        elementConfig: {
          type: "text",
          placeholder: "Country",
          required: true,
        },
        value: "",
        valid: false,
      },
      Email: {
        elementtype: "input",
        elementConfig: {
          type: "email",
          placeholder: "Example@gmail.com",
          required: true,
          pattern: "^.+@gmail.com$",
        },
        value: "",
        valid: false,
      },
      deliveryMethod: {
        elementtype: "select",
        elementConfig: {
          options: [
            { value: "fastest", displayValue: "Fastest" },
            { value: "cheapest", displayValue: "Cheapest" },
          ],
        },
        value: "fastest",
        valid: true,
      },
    },
    showSpinner: false,
    validForm: false,
  };

  orderHanlder = (event) => {
    event.preventDefault();
    this.setState({ showSpinner: true });
    let formData = {};
    for (let data in this.state.orderForm) {
      formData[data] = this.state.orderForm[data].value;
    }
    let data = {
      ingrediants: this.props.ingridents,
      totalPrice: this.props.price,
      orderData: formData,
    };

    axios
      .post("/order.json", data)
      .then(() => {
        this.setState({ showSpinner: false });
        this.props.history.push("/");
      })
      .catch((err) => {
        console.log(err.message);
        this.setState({ showSpinner: false });
      });
  };

  changedHandler = (event, index) => {
    const updatedOrderForm = {
      ...this.state.orderForm,
    };
    const updateFormElement = {
      ...updatedOrderForm[index],
    };
    updateFormElement.value = event.target.value;
    updateFormElement.valid = event.target.value.trim() !== "";
    if (updateFormElement.valid) {
      event.target.style.borderBottom = "2px solid green";
    } else {
      event.target.style.borderBottom = "2px solid red";
    }

    let checkvalid = true;
    for (let i in updatedOrderForm) {
      checkvalid = updatedOrderForm[i].valid === true && checkvalid;
    }
    updatedOrderForm[index] = updateFormElement;
    this.setState({ orderForm: updatedOrderForm, validForm: checkvalid });
  };

  render() {
    let forElementArray = [];
    for (let i in this.state.orderForm) {
      forElementArray.push({
        id: i,
        config: this.state.orderForm[i],
      });
    }
    let form = this.state.loading ? (
      <Spinner />
    ) : (
      <form onSubmit={this.orderHanlder}>
        {forElementArray.map((ele) => (
          <Input
            key={ele.id}
            elementtype={ele.config.elementtype}
            elementConfig={ele.config.elementConfig}
            value={ele.config.value}
            changed={(event) => this.changedHandler(event, ele.id)}
          />
        ))}
        <Button btnType="Success" disabled={!this.state.validForm}>
          ORDER
        </Button>
      </form>
    );
    return (
      <div className={classes.ContactData}>
        <h4>Enter your contact data</h4>
        {form}
      </div>
    );
  }
}

export default ContactData;
